import {
    ON_NOTIFICATION
} from './types';
import { INotification } from '../../types/interfaces'

export const turnNotification = ({ title, description, duration, show,type }: INotification) => async (
    dispatch: Function
) => {
    dispatch({
        type: ON_NOTIFICATION, payload: {
            title,
            description,
            duration,
            show,
            type
        }
    });
}

