import {
  REGISTER_TRIGGER,
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  LOGIN_TRIGGER,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT_SUCCESS,
  CHANGE_ACTIVE_TAB,
  ON_LOGIN_CHANGE,
  GET_CAPTCHA_TOKEN,
  TOGGLE_REGISTER_MODAL,
  ON_FORGET_PASS_CHANGE,
  TOGGLE_FORGET_PASS_MODAL,
  TOGGLE_LOGOUT_MODAL,
  PASSWORD_CHANGING,
  VERIFY_CODE,
  TOGGLE_VERIFY_CODE_MODAL,
  PASSWORD_CHANGING_TOKEN
} from '../actions/types';

const initialState = {
  isAuthenticated: localStorage.getItem('token') ? true : false,
  activeTab: 1,
  token: localStorage.getItem('token'),
  onGetCaptchaToken: '',
  login: {
    loading: false,
    username: "",
    password: "",
    authorization_status: {},
    verifyView: false,
    verifyCode: "",
    loadingVerify: false
  },
  register: {
    loading: false,
    modal: false
  },
  forgetPass: {
    modal: false,
    loading: false,
    username: ""
  },
  changePassword: {
    modal: false,
    loading: false,
    password: "",
    newPassword: "",
    newPasswordDuplicate: ""
  },
  changePasswordToken: {
    modal: false,
    loading: false,
    email: "",
    token: "",
    newPassword: "",
    newPasswordDuplicate: ""
  },
  verifyCode: {
    modal: false,
    loading: false,
    loadingSendCode: false,
    code: "",
  },
  logout: {
    modal: false
  }
};

export default function (state = initialState, action: any) {
  switch (action.type) {

    case LOGIN_TRIGGER: {
      return {
        ...state,
        login: { ...state.login, loading: true }
      }
    }
    case GET_CAPTCHA_TOKEN: {
      const value = action.payload;
      return {
        ...state,
        onGetCaptchaToken: value
      };
    }
    case ON_LOGIN_CHANGE: {
      const { prop, value } = action.payload;
      return {
        ...state,
        login: {
          ...state.login,
          [prop]: value,
        }
      };
    }
    case LOGIN_SUCCESS: {
      const loginRes = action.payload.user
      return {
        ...state,
        isAuthenticated: true,
        token: loginRes.token,
        login: {
          loading: false,
          authorization_status: loginRes.verify
        }
      };
    }
    case LOGIN_FAIL: {
      return {
        ...state,
        isAuthenticated: false,
        token: '',
        login: {
          ...state.login,
          loading: false,
        }
      }
    }
    case LOGOUT_SUCCESS: {
      return {
        ...state,
        isAuthenticated: false,
        token: ''
      };
    }
    case REGISTER_TRIGGER: {
      return {
        ...state,
        register: { ...state.register, loading: true }
      }
    }
    case REGISTER_SUCCESS: {
      return {
        ...state,
        register: {
          loading: false,
        }
      }
    }
    case REGISTER_FAIL: {
      return {
        ...state,
        register: {
          loading: false,
        }
      }
    }
    case CHANGE_ACTIVE_TAB: {
      return {
        ...state,
        activeTab: action.payload,
      }
    }
    case TOGGLE_REGISTER_MODAL: {
      return {
        ...state,
        register: {
          ...state.register,
          modal: action.payload
        }
      }
    }
    case ON_FORGET_PASS_CHANGE: {
      const { prop, value } = action.payload;
      return {
        ...state,
        forgetPass: {
          ...state.forgetPass,
          [prop]: value,
        }
      };
    }
    case TOGGLE_FORGET_PASS_MODAL: {
      return {
        ...state,
        forgetPass: {
          ...state.forgetPass,
          modal: action.payload
        }
      }
    }
    case TOGGLE_LOGOUT_MODAL: {
      return {
        ...state,
        logout: {
          ...state.logout,
          modal: action.payload
        }
      }
    }
    case PASSWORD_CHANGING: {
      const { prop, value } = action.payload;
      return {
        ...state,
        changePassword: {
          ...state.changePassword,
          [prop]: value,
        }
      };
    }
    case PASSWORD_CHANGING_TOKEN: {
      const { prop, value } = action.payload;
      return {
        ...state,
        changePasswordToken: {
          ...state.changePasswordToken,
          [prop]: value,
        }
      };
    }
    case VERIFY_CODE: {
      const { prop, value } = action.payload;
      return {
        ...state,
        verifyCode: {
          ...state.verifyCode,
          [prop]: value,
        }
      };
    }
    case TOGGLE_VERIFY_CODE_MODAL: {
      return {
        ...state,
        verifyCode: {
          ...state.verifyCode,
          modal: action.payload
        }
      }
    }
    default:
      return state;
  }
}
